import { utils, write } from 'xlsx';

const createBufferBlobANDDownload = ({ workbook, fileName }) => {
	const excelBuffer = write(workbook, { type: 'array', bookType: 'xlsx' });
	const blob = new Blob([excelBuffer], { type: 'array', bookType: 'xlsx' });
	const url = window.URL.createObjectURL(blob);
	const link = document.createElement('a');
	link.href = url;
	link.setAttribute('download', `${fileName} - ${new Date().toISOString().split('T')[0]}.xlsx`);
	document.body.appendChild(link);
	link.click();
};

const frequencyObjectToArray = (obj) => {
	const sheets = {};
	const sheetNames = Object.keys(obj);

	sheetNames.forEach((sheetName) => {
		const grid = [];
		const sheetData = obj[sheetName];
		const sheetDataKeys = Object.keys(sheetData);

		for (let i = 0; i < sheetDataKeys.length; i++) {
			const row = [];
			// family system, gender etc
			const rowHeader = sheetDataKeys[i];
			row.push(rowHeader);
			row.push('');

			// {male: 0, female: 100}
			const singleObject = sheetData[rowHeader];
			for (const [key, value] of Object.entries(singleObject)) {
				row.push(key);
				row.push(value);
			}

			// now a single row is ready, push it to the grid
			grid.push(row);
		}

		// now a sheet is ready, push it to the sheets
		sheets[sheetName] = grid;
	});
	return sheets;
};

async function createFullXLSXFile({ data, center }) {
	const { reportData } = data;
	try {
		const patient_aoa = [];
		const caregiver_aoa = [];
		const child_aoa = [];
		const workbook = utils.book_new();

		reportData.forEach((form) => {
			patient_aoa.push([...form.patientDemoData, ...form.patientSectionData]);
			caregiver_aoa.push([...form.caregiverDemoData, ...form.caregiverSectionData]);
			child_aoa.push([...form.childDemoData, ...form.childSectionData]);
		});

		// creating worksheet, we also have a function ustils.aoa_to_sheet() to convert array of arrays into worksheet
		const patientWorksheet = utils.aoa_to_sheet(patient_aoa);
		const caregiverWorksheet = utils.aoa_to_sheet(caregiver_aoa);
		const childWorksheet = utils.aoa_to_sheet(child_aoa);

		// adding worksheet to workbook
		utils.book_append_sheet(workbook, patientWorksheet, 'Patient');
		utils.book_append_sheet(workbook, caregiverWorksheet, 'Caregiver');
		utils.book_append_sheet(workbook, childWorksheet, 'Child');

		const fileName = `Full Report ${center && `- ${center}`}`;

		createBufferBlobANDDownload({
			workbook,
			fileName
		});

		return true;
	} catch (err) {
		return new Promise((_, reject) => {
			reject(false);
		});
	}
}

async function createFrequencyXLSXFile({ data, center }) {
	try {
		const workbook = utils.book_new();

		const sheets = frequencyObjectToArray(data);
		const sheetNames = Object.keys(sheets);
		for (let i = 0; i < sheetNames.length; i++) {
			const sheetName = sheetNames[i];
			const worksheet = utils.aoa_to_sheet(sheets[sheetName]);
			const columnWidths = [
				{ wch: 50 },
				{ wch: 5 },
			];
			worksheet['!cols'] = columnWidths;
			utils.book_append_sheet(workbook, worksheet, sheetName);
		}

		createBufferBlobANDDownload({
			workbook,
			fileName: `Frequency Report ${center && `- ${center}`}`
		});

		return true;
	} catch (err) {
		throw new Error(err.message);
	}
}

async function createMeanXLSXFile({ data, center }) {
	try {
		const workbook = utils.book_new();

		for (let i = 0; i < data.length; i++) {
			const entityData = data[i];
			const worksheet = utils.aoa_to_sheet([['Questions', 'Averages'], ...entityData.averages.map((singleAverage) => [singleAverage.question_text, singleAverage.avg])]);
			const columnWidths = [
				{ wch: 50 },
				{ wch: 10 },
			];
			worksheet['!cols'] = columnWidths;
			utils.book_append_sheet(workbook, worksheet, entityData.entity);
		}

		createBufferBlobANDDownload({
			workbook,
			fileName: `Mean Report ${center && `- ${center}`}`
		});

		return true;
	} catch (err) {
		throw new Error(err.message);
	}
}

export { createFullXLSXFile, createFrequencyXLSXFile, createMeanXLSXFile };;