import axiosInstance from '../../../utils/axios.middleware.js';

class User {

	static getFullReport = (form) => {

		let apiPath = `/api/report/date?startingDate=${form.startingDate}&endingDate=${form.endingDate}&city_id=${form.city_id}`;
		return axiosInstance.get(apiPath);

	};

	static getFrequencyReport = (form) => {

		let apiPath = `/api/report/frequency?startingDate=${form.startingDate}&endingDate=${form.endingDate}&city_id=${form.city_id}`;
		return axiosInstance.get(apiPath);

	};

	static getMeanReport = (form) => {

		let apiPath = `/api/report/mean?startingDate=${form.startingDate}&endingDate=${form.endingDate}&city_id=${form.city_id}`;
		return axiosInstance.get(apiPath);

	};

}

export default User;