import Report from '../services/report/report.js';
import actionErrorHandler from '../../helpers/actionErrorHandler.js';

export const getFullReport = (form) => {
	return Report.getFullReport(form)
		.then((response) => {
			return {
				status: response.status,
				data: response.data,
				message: "Success",
			};
		})
		.catch((e) => {
			return actionErrorHandler(e);
		});
};

